import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			loadingType: 'more',
			orderList:{},
			page:0,
			navList: [{
					state: 0,
					text: '全部'
				},
				{
					state: 1,
					text: '待付款'
				},
				{
					state: 3,
					text: '待收货'
				},
				{
					state: 5,
					text: '已完成'
				}
			],
		};
	},
	onShow() {
		
	},
	onLoad(options){
		if(options.state == undefined){
			options.state = 0;
		}
		this.loadData();
	},	
	//下拉刷新
	onPullDownRefresh() {
	    this.loadData("refresh");
	},
	//加载更多
	onReachBottom() {
	    this.loadData('more');
	},
	methods: {
		//获取订单列表
		loadData(type='init', loading){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			
			//没有更多直接返回
			if(type === 'refresh' || type == 'init'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more';
			}
			if(type === 'refresh' || type == 'init' || type=='tabChange'){
				this.orderList = [];
				this.page = 0;
			}
			navItem.loadingType = 'loading';
			
			setTimeout(()=>{
				this.page = this.page + 1;
				uni.request({
					url: this.$Url + '/clmsj/order/userOrderList', // http://bull.hnw.net/index/bull/client_wash_info
					data: {
						page:this.page,
						state: state
					},
					header:{ 'auth' : uni.getStorageSync('session_key') },
					method:'POST',
					success: (res) => {
						uni.hideLoading();
						if (res.data.code == 0) {
							let orderList = res.data.data;
							orderList.forEach(item=>{
								item = Object.assign(item, this.orderStateExp(item.state));
							})
							
							this.orderList = this.orderList.concat(orderList);
							console.log(this.orderList)
						}
					},
				})
				//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
				this.$set(navItem, 'loaded', true);
				
				//判断是否还有数据， 有改为 more， 没有改为noMore 
				navItem.loadingType = 'more';
			}, 600);	
		}, 

		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			this.loadData('refresh');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		},
		viewExpress(item){
			console.log('viewExpress')
			uni.navigateToMiniProgram({
				appId: 'wx6885acbedba59c14',
				path: 'pages/result/result?nu='+ item.express_number +'&com=&querysource=third_xcx',
				success(res) {
					// 打开成功
				}
			})
		},
		orderDetail(item){
			console.log('order detail')
			uni.navigateTo({
				url:'/pages/order/detail?id=' + item.id
			})
		},
		//删除订单
		deleteOrder(index){
			// uni.showLoading({
			// 	title: '请稍后'
			// })
			setTimeout(()=>{
				this.orderList.splice(index, 1);
				// uni.hideLoading();
			}, 600)
		},
		//订单支付
		payment(order_sn){
			uni.request({
				url: this.$Url + '/clmsj/order/getPayParam',
				data: { order_sn: order_sn },
				header:{ 'auth' : uni.getStorageSync('session_key') },
				success: (resp) => {
					let res = resp.data.data;
					// 调起支付
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: res.timeStamp,
						nonceStr: res.nonceStr,
						package: res.package,
						signType: 'MD5',
						paySign: res.paySign,
						success: (res) => {
							uni.showToast({
								title:'支付成功'
							});
							uni.redirectTo({
								url: '/pages/money/paySuccess?order_sn='+this.order_sn
							})
						},
						fail: (err)=> {
							console.log('fail:' + JSON.stringify(err));
							uni.showToast({
								icon:'none',
								title:'支付失败'
							})
						}
					});
				}
			});
		},
		//订单操作
		handleOrder(item,index,state){
			
			let tipMsg = '您确认要进行该操作吗？';
			if(state == 9){
				tipMsg = '您确认要取消订单吗？';
			}else if(state==4){
				tipMsg = '请确认您已收到货';
			}
			uni.showModal({
			    title: '提示',
			    content: tipMsg,
			    success: (resC=>{
					if(resC.confirm){
						uni.showLoading({
							title: '请稍后'
						})
						let {stateTip, stateTipColor} = this.orderStateExp(state);
						item = Object.assign(item, {
							state: state,
							stateTip, 
							stateTipColor
						})
						uni.request({
							url: this.$Url + '/clmsj/order/userUpdateOrderStatus', // http://bull.hnw.net/index/bull/client_wash_info
							data: {
								id:item.id,
								state:state
							},
							header:{ 'auth' : uni.getStorageSync('session_key') },
							method:'POST',
							success: (res) => {
								uni.hideLoading();
								if (res.data.code == 0) {
									this.orderList[index].state = state;
									this.$api.msg('操作成功');
								}
							},
						})
						uni.hideLoading();
					}
					
				})
			})
			
		},

		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待发货'; break;
				case 3:
					stateTip = '已发货，待收货'; break;
				case 4:
					stateTip = '已收货'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
				//更多自定义
			}
			return {stateTip, stateTipColor};
		}
	},
}
